.plans-container{
    display: flex;
    flex-direction: column;
    gap: 4rem;
    margin-top: -4rem;
    padding: 0 2rem;
    position: relative;
}

.plans-header{
    display: flex;
    gap: 2rem;
    font-weight: bold;
    font-size: 3.5rem;
    justify-content: center;
    color: white;
    text-transform: uppercase;
    font-style: italic;
}

.plans{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}

.plan{
    display: flex;
    flex-direction: column;
    background: var(--caloryCard);
    color: white;
    gap: 2rem;
    padding:1.5rem;
    width: 20rem;
}

.plan:nth-child(2){
    background: var(--planCard);
    transform: scale(1.1);
}

.plan:hover{
    background: var(--planCard);
    transform: scale(1.1);
}

.plan>:nth-child(2){
    font-size: 1rem;
    font-weight: bold;
}

.plan>:nth-child(3){
    font-size: 3rem;
    font-weight: bold;
}


.plan>:nth-child(5){
    font-size: 0.8rem;
    /* font-weight: bold; */
}

.features{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.feature{
    display: flex;
    align-items: center;
    gap: 1rem;
}

.plan>.btn:hover{
    color: orange;
}

.blur-p-1{
    width: 23rem;
    height: 23rem;
    top: 6rem;
    left: 0rem;
}

.blur-p-2{
    width: 32rem;
    height: 23rem;
    top: 10rem;
    right: 0rem;
}

@media screen and (max-width:768px) {
    .plans{
        flex-direction: column;
    }

    .plans-header{
        display: flex;
        flex-direction: column;
        gap: 1rem;
        font-size: x-large;
        align-items: center;
        justify-content: center;
    }

    .plans{
        margin-top: -17rem;
        transform: scale(0.7);
    }
}